<template>
  <div id="app">
    <h1>Test project</h1>
    <br>
    <div class="resSpace">
      {{resData}}
    </div>
    <div class="paramHttpReq">
      <h3>Http request</h3>
      <label for="">Key 1:</label>
      <input type="text" v-model="parKey1">
      <br>
      <br>
      <label for="">Value 1:</label>
      <input type="text" v-model="parValue1">
      <br><br><br>
      <label for="">Key 2:</label>
      <input type="text" v-model="parKey2">
      <br>
      <br>
      <label for="">Value 2:</label>
      <input type="text" v-model="parValue2">
      <br>
      <br>
      <small v-if="responseCode">{{responseCode == 200 ? responseCode + ' (Success)' : responseCode}}</small>
      <br>
      <button @click="sendAPIRequest()">Send</button>
      <button @click="addItem()">Save</button>
    </div>
    <br>
    <div class="savedParams">
      <h3>Saved params</h3>
      <div class="param" v-for="(param, index) in retrievedParams" :key="index" @click="adjustInput(index, $event)">
        <span v-show="param.key1 && param.value1">{{param.key1}}: {{param.value1}}</span>
        <br>
        <span v-show="param.key2 && param.value2">{{param.key2}}: {{param.value2}}</span>
        <br>
        <button @click="removeItem(index)">Remove</button>
      </div>
    </div>
  </div>
</template>

<script>
import db from '@/main'
import axios from 'axios'

export default {
  data() {
    return {
      retrievedParams: [],
      parKey1: '',
      parValue1: '',
      parKey2: '',
      parValue2: '',
      responseCode: null,
      resData: 'Waiting for request...'
    }
  },

  created() {
    this.refreshItems()
  },

  methods: {
    refreshItems() {
      this.retrievedParams = []
      this.parKey1 = ''
      this.parValue1 = ''
      this.parKey2 = ''
      this.parValue2 = ''
      db.collection("table1").get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.retrievedParams.push({id: doc.id, key1: doc.data().key1, value1: doc.data().value1, key2: doc.data().key2, value2: doc.data().value2})
          });
      });
    },

    adjustInput(index, event) {
      if (event.srcElement.tagName !== "BUTTON") {
        this.parKey1 = this.retrievedParams[index].key1
        this.parValue1 = this.retrievedParams[index].value1
        this.parKey2 = this.retrievedParams[index].key2
        this.parValue2 = this.retrievedParams[index].value2
      }
    },

    sendAPIRequest() {
      if(this.parValue1 == '' && this.parValue2 == '' && this.parKey1 == '' && this.parKey2 == '' || ((this.parValue1 !== '' && this.parKey1 == '') || (this.parValue1 == '' && this.parKey1 !== '')) || ((this.parValue2 !== '' && this.parKey2 == '') || (this.parValue2 == '' && this.parKey2 !== ''))) {
        alert('Please fill out the fields.')
      } else {
        axios.get(`https://postman-echo.com/get?${(this.parKey1 !== '' && this.parValue1 !== '') ? (this.parKey1 + '=' + this.parValue1) : ''}${(this.parKey1 !== '' && this.parValue1 !== '' && this.parKey2 !== '' && this.parValue2 !== '') ? '&' : ''}${(this.parKey2 !== '' && this.parValue2 !== '') ? (this.parKey2 + '=' + this.parValue2) : ''}`)
        .then(res => {
          this.refreshItems()
          this.responseCode = res.status
          this.resData = res
        })
      }
    },

    addItem() {
      if(this.parValue1 == '' && this.parValue2 == '' && this.parKey1 == '' && this.parKey2 == '' || ((this.parValue1 !== '' && this.parKey1 == '') || (this.parValue1 == '' && this.parKey1 !== '')) || ((this.parValue2 !== '' && this.parKey2 == '') || (this.parValue2 == '' && this.parKey2 !== ''))) {
        alert('Please fill out the fields.')
      } else {
        db.collection("table1").doc().set({
          key1: this.parValue1 !== '' ? this.parKey1 : '',
          value1: this.parKey1 !== '' ? this.parValue1 : '',
          key2: this.parValue2 !== '' ? this.parKey2 : '',
          value2: this.parKey2 !== '' ? this.parValue2 : ''
        }).then(() => this.refreshItems())
      }
    },

    removeItem(index) {
      db.collection("table1").doc(this.retrievedParams[index].id).delete().then(() => this.refreshItems())
    }
  }
}
</script>

<style lang="sass">
#app 
  font-family: 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  text-align: center
  color: #2c3e50
  margin-top: 60px

.paramHttpReq
  button
    cursor: pointer
    margin: 20px
    padding: 5px 10px
  label
    margin-right: 10px

.param
  cursor: pointer
  border: 2px #333 solid
  width: 300px
  margin: 30px auto
  padding: 20px
  border-radius: 20px
  span
    display: block
    margin: 5px 0
  &:hover
    background: #333
    color: #fff 

.resSpace
  width: 25vw
  height: 25vw
  padding: 40px
  border: 2px #333 solid
  border-radius: 20px
  overflow-y: scroll
  position: absolute
  right: 70px
  word-break: break-all

</style>
